<template>
	<div id="seckill-create">
		<public-header title="订单确认"></public-header>
		<div class="content">
			<div class="info">
				<div class="store van-hairline--bottom">
					<img class="logo" :src="store.logo" alt="">
					<div class="title">{{store.title}}</div>
				</div>
				<div class="goods van-hairline--bottom">
					<img :src="goods.thumb" alt="">
					<div class="goods-info">
						<div class="goods-title">
							{{goods.title}}
							<div class="desc">{{goods.content}}</div>
						</div>
						<div class="goods-price">¥{{goods.price}}</div>
					</div>
				</div>
				<div class="nums van-hairline--bottom">
					<div>数量</div>
						<div class="goods-handle">
						<van-icon name='minus' @click="onMinus"></van-icon>
						<span>{{num}}</span>
						<van-icon name='plus' @click="onPlus"></van-icon>
					</div>
				</div>
				<div class="total">
					<div>小计</div>
					<div class="total-price">¥{{goods.price*num}}</div>
				</div>
			</div>
			<van-cell-group>
				<van-field
					v-model="username"
					label="联系人"
					placeholder="请输入联系人姓名"
				/>
				<van-field
					v-model="mobile"
					type="number"
					label="联系电话"
					placeholder="请输入联系电话"
				/>
			</van-cell-group>
		</div>
		<van-submit-bar :disabled="!islegal" :price="goods.price*num*100" label="待支付" button-text="提交订单" @submit="onSubmit"></van-submit-bar>
		<transition name="loading">
			<iloading v-if="showPreLoading"></iloading>
		</transition>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	export default {
		components: {
			PublicHeader
		},
		data() {
			return {
				id: 0,
				num: 1,
				islegal: false,
				goods: {},
				store: {},
				username: '',
				mobile: '',
				showPreLoading: true,
			}
		},
		methods: {
			onLoad() {
				this.util.request({
					url: 'seckill/seckill/detail',
					data: {
						id: this.id,
					}
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message)
						return;
					}
					result = result.message;
					this.goods = result.goods;
					this.store = result.store;
					if(!this.goods.end) {
						this.islegal = true;
					}
					this.showPreLoading = false;
				})
			},
			onMinus() {
				var num = this.num - 1;
				if(num < 1) {
					num = 1;
				}
				this.num = num;
			},
			onPlus() {
				var num = this.num + 1;
				var max = this.goods.total - this.goods.sailed;
				if(num > max) {
					num = max;
					this.util.$toast('商品数量已达最大限制');
				}
				this.num = num;
			},
			onSubmit() {
				if(!this.islegal) {
					return false;
				}
				if(!this.username) {
					this.util.$toast('请输入联系人姓名');
					return false;
				}
				if(!this.mobile) {
					this.util.$toast('请输入联系人电话');
					return false;
				}
				this.islegal = false;
				this.util.request({
					url: 'seckill/seckill/create',
					data: {
						id: this.id,
						goods_num: this.num,
						username: this.username,
						mobile: this.mobile,
					}
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message)
						return;
					}
					var order_id = result.message;
					this.util.$toast('下单成功', this.util.getUrl({path: '/pages/public/pay', query: {order_id: order_id, order_type: 'seckill'}}));
				})
			}
		},
		created() {
			if(this.$route.query.id) {
				this.id = this.$route.query.id;
			} else {
				return false;
			}
		},
		mounted() {
			this.onLoad();
		},
	}
</script>

<style>
	#seckill-create .content{
		position: fixed;
		top: 46px;
		right: 0;
		left: 0;
		bottom: 0;
		width: 100%;
		background: #f5f5f5;
		overflow-x: hidden;
		overflow-y: scroll;
	}
	#seckill-create .content::-webkit-scrollbar {
		display: none;
	}
	#seckill-create .content .info{
		background-color:#fff;
		padding-left:15px;
		width:100%;
	}
	#seckill-create .content .info .store{
		display:flex;
		flex-flow:row nowrap;
		justify-content:space-between;
		align-items:center;
		height:44px;
		padding-right:15px;
	}
	#seckill-create .content .info .store img{
		width:30px;
		height:30px;
		border-radius:5px;
		margin-right:5px;
	}
	#seckill-create .content .info .store .title{
		width: 90%;
		font-size: 15px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	#seckill-create .content .info .goods{
		display:flex;
		flex-flow:row nowrap;
		justify-content:space-between;
		align-items:center;
		padding:10px 15px 10px 0;
	}
	#seckill-create .content .info .goods img{
		width:75px;
		height:75px;
		border: none;
	}
	#seckill-create .content .info .goods .goods-info{
		width:75%;
		height:75px;
		line-height:1;
		display:flex;
		justify-content:space-between;
	}
	#seckill-create .content .info .goods .goods-info .goods-title{
		width: 78%;
		overflow:hidden;
		text-overflow:ellipsis;
		white-space:nowrap;
	}
	#seckill-create .content .info .goods .goods-info .goods-title .desc{
		margin-top:10px;
		font-size:13px;
		color:#A1A1A1;
		line-height:1.5;
		max-width:100%;
		overflow:hidden;
		text-overflow:ellipsis;
		white-space:nowrap;
	}
	#seckill-create .content .info .goods .goods-info .goods-price{
		color:#B1B1B1;
		font-size:14px;
	}
	#seckill-create .content .info .nums{
		display:flex;
		flex-flow:row nowrap;
		justify-content:space-between;
		align-items:center;
		height:44px;
		padding-right:15px;
	}
	#seckill-create .content .info .nums .goods-handle .van-icon-minus{
		width:30px;
		height:30px;
		border-radius:50%;
		text-align:center;
		line-height:30px;
		box-sizing:border-box;
		border:1px solid #ccc;
		color:#C7C7C7;
		font-size:18px;
	}
	#seckill-create .content .info .nums .goods-handle .van-icon-plus{
		width:30px;
		height:30px;
		border-radius:50%;
		text-align:center;
		line-height:30px;
		box-sizing:border-box;
		border:1px solid #ccc;
		font-size:18px;
		color:#fff;
		background-color:#ff2d4b;
		border-color:#ff2d4b;
	}
	#seckill-create .content .info .nums .goods-handle span{
		padding: 0 10px;
	}
	#seckill-create .content .info .total{
		display:flex;
		flex-flow:row nowrap;
		justify-content:space-between;
		align-items:center;
		height:44px;
		padding-right:15px;
	}
	#seckill-create .content .info .total .total-price{
		color:#ff2d4b;
		font-size:16px;
	}
	#seckill-create .content .van-cell-group{
		margin-top:10px;
	}
	#seckill-create .content .order-btn-field{
		position: fixed;
		bottom:  0;
		left: 0;
		width:100%;
		height:50px;
		background-color:#fff;
	}
	#seckill-create .content .order-btn-field .order-submit{
		float:right;
		width:29.5%;
		height:50px;
		font-size:16px;
		color:#fff;
		background-color:#ff2d4b;
		text-align:center;
		line-height:50px;
		border-radius:0;
		border: none;
	}
	#seckill-create .content .order-btn-field .order-total-field{
		float:right;
		margin-right:10px;
		height:50px;
		line-height:50px;
		color:#2f2f2f;
		font-size:14px;
	}
	#seckill-create .content .order-btn-field .order-total-field .order-total{
		display:inline-block;
		color:#F95339;
		font-size:21px;
		vertical-align:middle;
		margin-left:5px;
	}
	#seckill-create .content .order-btn-field .order-benefit{
		display:inline-block;
		height:50px;
		margin-left:15px;
		font-size:14px;
		line-height:50px;
		overflow:hidden;
		text-overflow:ellipsis;
		white-space:nowrap;
	}
	#seckill-create .content .order-btn-field .order-benefit div{
		display:inline-block;
		margin-left:5px;
	}
</style>


